<template>
  <form class="member-form" @submit.prevent>
    <fieldset>
      <legend class="tit-h2">
        {{ isAlone ? "Данные участника:" : "Данные капитана:" }}
      </legend>

      <div class="member-form__item">
        <label for="email" class="label-title">Email:</label>
        <input
          type="email"
          id="email"
          class="default-input"
          :class="{ _invalid: !validations.email }"
          placeholder="email"
          v-model="memberForm.email"
          required
          @blur="validateField('email')"
        />
      </div>

      <div class="member-form__item">
        <label for="phone" class="label-title">Номер телефона:</label>
        <input
          type="tel"
          name="phone"
          id="phone"
          class="default-input"
          :class="{ _invalid: !validations.phone }"
          placeholder="+7"
          v-model.number="memberForm.phone"
          required
          @blur="validateField('phone')"
        />
      </div>

      <div class="member-form__item" v-if="!isAlone">
        <label for="team-name" class="label-title">Название команды:</label>
        <input
          type="text"
          id="team-name"
          class="default-input"
          :class="{ _invalid: !validations.teamName }"
          placeholder="Название"
          v-model="memberForm.teamName"
          :required="!isAlone"
          @blur="validateField('teamName')"
        />
      </div>

      <div class="member-form__item">
        <label for="first-name" class="label-title">Имя:</label>
        <input
          type="text"
          id="first-name"
          class="default-input"
          :class="{ _invalid: !validations.name }"
          placeholder="Имя"
          v-model="memberForm.name"
          required
          @blur="validateField('name')"
        />
      </div>

      <div class="member-form__item">
        <label for="last-name" class="label-title">Фамилия:</label>
        <input
          type="text"
          id="last-name"
          class="default-input"
          :class="{ _invalid: !validations.lastName }"
          placeholder="Фамилия капитана"
          v-model="memberForm.lastName"
          required
          @blur="validateField('lastName')"
        />
      </div>

      <div class="member-form__item">
        <label for="birth-date" class="label-title">Дата рождения:</label>
        <input
          type="date"
          id="birth-date"
          class="default-input"
          :class="{ _invalid: !validations.dateBorn }"
          v-model="memberForm.dateBorn"
          required
          @blur="validateField('dateBorn')"
        />
        <span v-if="!validations.dateBorn" class="error-message"
          >Вам должно быть не менее 18 лет.</span
        >
      </div>
    </fieldset>

    <fieldset>
      <div class="member-form__link">
        <check-input
          v-model="checkboxes.marketingConsent"
          id="checgdkId3"
          name="n65game34"
        />
        <p class="default-link">
          Я даю согласие на отправку информационно-рекламной рассылки
          организации ИНН 7813635955
        </p>
      </div>

      <div class="member-form__link">
        <check-input
          v-model="checkboxes.dataProcessingConsent"
          id="cheddcckId3"
          name="nadxvme34"
        />
        <div
          class="default-link"
          @click="store.state.documents.showPrivacyPolicy = true"
        >
          Я предоставляю согласие на обработку своих персональных данных
          организации ИНН 7813635955
        </div>
      </div>

      <div class="member-form__link member-form__left" v-if="currentEventDocumnet?.link">
        <check-input
          v-model="checkboxes.competitionRulesConsent"
          id="checkId343"
          name="name4"
        />
        <a
          :href="currentEventDocumnet.link"
          target="_blank"
          class="default-link"
          >Ознакомлен с Положением о соревнованиях</a
        >
      </div>

      <div class="member-form__link">
        <check-input
          :modelValue="checkValue"
          :disabled="false"
          class="hide-element"
        />
        <div class="">
          <span class="default-link"
            >Бронь считается подтвержденной только после заполнения формы
            регистрации и предоплаты 50% за каждый этап. К сожалению, если
            отмена происходит позже чем за 5 дней до этапа, предоплата не
            возвращается.</span
          >
          <div>
            <br>
            Общая стоимость: {{ allPrice.toLocaleString() }} ₽
            <br />
            Цена бронирования: {{ (allPrice / 2).toLocaleString() }} ₽
          </div>
        </div>
      </div>

      <div class="member-form__link">
        <check-input
          v-model="checkboxes.bookingAgreementConsent"
          :value="false"
          id="checkId3"
          name="name34"
        />
        <a href="" class="default-link"
          >Принимаю условия соглашения и бронирования</a
        >
      </div>
    </fieldset>

    <button
      class="big-btn _active w-100"
      @click="validateForm"
      :disabled="!isFormReady || createdOrdersLoading"
    >
      Регистрация
    </button>
  </form>
</template>

<script lang="ts" setup>
import { useStore } from "vuex";
import checkInput from "@/components/UI/check-input.vue";
import { computed, reactive } from "vue";
import isHaveFreePlaces from "@/functions/isHaveFreePlaces";

const store = useStore();

const stepsFields = computed(() => store.state.steps.stepsFields);
const memberForm = computed(() => store.state.steps.memberForm);
const isAlone = computed(() => stepsFields.value.isAlone);
const selectedEvent = computed(() => store.state.selectedEvent);

const validations = reactive({
  email: true,
  phone: true,
  teamName: true,
  name: true,
  lastName: true,
  dateBorn: true,
});

const checkboxes = reactive({
  marketingConsent: false,
  dataProcessingConsent: false,
  competitionRulesConsent: false,
  bookingAgreementConsent: false,
});

const isFormReady = computed(() => {
  return Object.values(checkboxes).every(Boolean);
});

function validateField(field) {
  switch (field) {
    case "email": {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      validations.email = emailPattern.test(memberForm.value.email);
      break;
    }
    case "phone": {
      validations.phone = !!memberForm.value.phone;
      break;
    }
    case "teamName": {
      validations.teamName = isAlone.value || !!memberForm.value.teamName;
      break;
    }
    case "name": {
      validations.name = memberForm.value.name.length >= 2;
      break;
    }
    case "lastName": {
      validations.lastName = memberForm.value.lastName.length >= 2;
      break;
    }
    case "dateBorn": {
      const today = new Date();
      const birthDate = new Date(memberForm.value.dateBorn);
      let age = today.getFullYear() - birthDate.getFullYear();
      const monthDiff = today.getMonth() - birthDate.getMonth();
      const dayDiff = today.getDate() - birthDate.getDate();
      if (monthDiff < 0 || (monthDiff === 0 && dayDiff < 0)) {
        age--;
      }
      validations.dateBorn = age >= 18;
      break;
    }
  }
}

function validateForm() {
  validateField("email");
  validateField("phone");
  validateField("teamName");
  validateField("name");
  validateField("lastName");
  validateField("dateBorn");

  const isFormValid = Object.values(validations).every(Boolean);

  if (isFormValid && isFormReady.value) {
    handleValidForm();
  }
}

function handleValidForm() {
  console.log("Форма успешно валидирована и готова к отправке!");

  store.dispatch("createOrders");
}

const eventDocumnets = computed(() => store.state.documents.documentsForEvents);

const currentEventDocumnet = computed(() =>
  eventDocumnets.value.find((i) => i.eventId === selectedEvent.value.id)
);

const allPrice = computed(() => {
  let priceKey: "price_min" | "price_max" = "price_min";

  const isNeedInstructor = store.state.steps.stepsFields.instructor;

  if (isAlone.value || stepsFields.value.yachtType.btnText === "Яхта ZIGZAG") {
    priceKey = "price_max";
  }

  let result = 0;

  stepsFields.value.selectedStages.forEach((stage) => {
    if (isHaveFreePlaces(stage)) {
      result += stage.services[0].service[priceKey];
      if (isNeedInstructor) {
        result += 5000;
      }

      if (stage.name2) {
        result += stage.services2[0].service[priceKey];
        if (isNeedInstructor) {
          result += 5000;
        }
      }
    }
  });

  return result;
});

const createdOrdersLoading = computed(
  () => store.state.steps.createdOrdersLoading
);
</script>

<style lang="scss" scoped>
.member-form {
  &__item {
    margin-bottom: 44px;
  }

  .tit-h2 {
    margin-bottom: 19px;
  }

  &__link {
    display: flex;
    align-items: start;
    margin-bottom: 33px;
  }

  @media (max-width: 500px) {
    &__item {
      margin-bottom: 35px;
    }
    &__link {
      font-size: 13px;
      margin-bottom: 19px;
    }
  }

  ._invalid {
    border-color: red;
  }

  .error-message {
    color: red;
    font-size: 12px;
    margin-top: 5px;
    display: block;
  }
}
</style>
