import axios from "axios";

export default {
  state: {
    privacyPolicy: null,
    showPrivacyPolicy: false,

    documentsForEvents: [
      {
        eventId: 1,
        link: 'https://b24-flpzny.bitrix24.ru/~HszE4',
      },
      {
        eventId: 2,
        link: 'https://b24-flpzny.bitrix24.ru/~GUB1M',
      },
      {
        eventId: 3,
        link: 'https://b24-flpzny.bitrix24.ru/~pSs7j',
      },
      {
        eventId: 4,
        link: '',
      },
      {
        eventId: 5,
        link: 'https://b24-flpzny.bitrix24.ru/~IG0bc',
      },
    ]
  },
  getters: {},
  mutations: {
  },
  actions: {
    async getPrivacyPolicy(context:any) {
      return await axios({
        method: 'get',
        url: 'https://api.yclients.com/api/v1/booking/locations/1268324/privacy_policy',
        headers: {
          Authorization: context.rootState.auth,
        },
      }).then((res) => {
        context.state.privacyPolicy = res.data.data
      })
    }
  },
};
