<template>
  <div class="privacy-policy" v-if="privacyPolicy">
    <div class="privacy-policy__inner">
      <button class="close-button" @click="closePrivacyPolicy">×</button>
      <h2
        class="tit-h3"
        v-html="privacyPolicy.attributes.legal_entity_name"
      ></h2>
      <p>ИНН {{ privacyPolicy.id }}</p>

      <div
        class="privacy-policy__content"
        v-html="privacyPolicy.attributes.privacy_agreement_content"
      ></div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { useStore } from "vuex";

const store = useStore();

const privacyPolicy = computed(() => store.state.documents.privacyPolicy);

const closePrivacyPolicy = () => {
  store.state.documents.showPrivacyPolicy = false;
};
</script>

